import React, { useState } from 'react';
import Modal from 'react-modal';
import { StyledButton } from '../styles/UserModal.styled';
import { districtAndTalukInfo } from '../Helper';

const customStyles = {
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
  },
};

const UserModal = () => {
  const [isModalOpen, setIsModalOpen] = useState(true);
  const [isStudent, setIsStudent] = useState(false);
  const [taluks, setTaluks] = useState([]);

  const allDistricts = districtAndTalukInfo.map(({ code, name }) => ({ code, name }));

  const checkIsStudent = (e) => {
    const { value } = e.target;

    setIsStudent(value === 'yes');
  };

  const closeModal = () => {
    setIsModalOpen(false);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setIsModalOpen(false);
  };

  const addTaluks = (e) => {
    const { taluks: talukList } = districtAndTalukInfo.find(({ code }) => code === e.target.value);
    setTaluks(talukList);
  };
  return (
    <Modal isOpen={isModalOpen} style={customStyles} contentLabel="User Modal">
      <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
        <h2 style={{ padding: 0, marginTop: '-1rem' }}>User Details</h2>
        <button
          type="button"
          onClick={closeModal}
          style={{ border: 'none', backgroundColor: 'inherit', fontSize: '3rem', marginTop: '-2rem' }}
        >
          &times;
        </button>
      </div>

      <form
        onSubmit={handleSubmit}
        style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end', gap: '0.5rem' }}
      >
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <label htmlFor="name">Name</label>
          <input
            type="text"
            name="name"
            id="name"
            style={{ marginLeft: '0.5rem', width: '20rem', padding: '0.25rem 0.5rem' }}
          />
        </div>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <label htmlFor="dob">Date Of Birth</label>
          <input
            type="date"
            name="dob"
            id="dob"
            style={{ marginLeft: '0.5rem', width: '20rem', padding: '0.25rem 0.5rem' }}
          />
        </div>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <label htmlFor="gender">Gender</label>
          <select name="gender" id="gender" style={{ marginLeft: '0.5rem', width: '20rem', padding: '0.25rem 0.5rem' }}>
            <option value="">--SELECT--</option>
            <option value="male">Male</option>
            <option value="female">Female</option>
            <option value="other">Other</option>
          </select>
        </div>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <label htmlFor="email">Email</label>
          <input
            type="email"
            name="email"
            id="email"
            style={{ marginLeft: '0.5rem', width: '20rem', padding: '0.25rem 0.5rem' }}
          />
        </div>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <label htmlFor="phno">Phone Number</label>
          <input
            type="tel"
            name="phno"
            id="phno"
            style={{ marginLeft: '0.5rem', width: '20rem', padding: '0.25rem 0.5rem' }}
          />
        </div>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <label htmlFor="address">Address</label>
          <textarea
            name="address"
            id="address"
            style={{
              marginLeft: '0.5rem',
              width: '20rem',

              height: '6rem',
              padding: '0.25rem 0.5rem',
            }}
          />
        </div>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <label htmlFor="district">District</label>
          <select
            name="district"
            id="district"
            onChange={addTaluks}
            style={{ marginLeft: '0.5rem', width: '20rem', padding: '0.25rem 0.5rem' }}
          >
            <option value="">--SELECT--</option>
            {allDistricts.map(({ code, name }) => (
              <option key={code} value={code}>
                {name}
              </option>
            ))}
          </select>
        </div>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <label htmlFor="taluk">Taluk</label>
          <select name="taluk" id="taluk" style={{ marginLeft: '0.5rem', width: '20rem', padding: '0.25rem 0.5rem' }}>
            <option value="">--SELECT--</option>
            {taluks.map(({ code, name }) => (
              <option key={code} value={code}>
                {name}
              </option>
            ))}
          </select>
        </div>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <label htmlFor="religion">Religion</label>
          <input
            type="text"
            name="religion"
            id="religion"
            style={{ marginLeft: '0.5rem', width: '20rem', padding: '0.25rem 0.5rem' }}
          />
        </div>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <label htmlFor="student">Student</label>
          <select
            name="student"
            id="student"
            onChange={(e) => checkIsStudent(e)}
            style={{ marginLeft: '0.5rem', width: '20rem', padding: '0.25rem 0.5rem' }}
          >
            <option value="">--SELECT--</option>
            <option value="yes">Yes</option>
            <option value="no">No</option>
          </select>
        </div>
        {isStudent && (
          <>
            <div style={{ display: 'flex', alignItems: 'center' }}>
              <label htmlFor="schoolOrCollege">School/College</label>
              <input
                type="text"
                name="schoolOrCollege"
                id="schoolOrCollege"
                style={{ marginLeft: '0.5rem', width: '20rem', padding: '0.25rem 0.5rem' }}
              />
            </div>
            <div style={{ display: 'flex', alignItems: 'center' }}>
              <label htmlFor="classOrCourse">Class/Course</label>
              <input
                type="text"
                name="classOrCourse"
                id="classOrCourse"
                style={{ marginLeft: '0.5rem', width: '20rem', padding: '0.25rem 0.5rem' }}
              />
            </div>
          </>
        )}
        <StyledButton type="submit">Continue</StyledButton>
      </form>
    </Modal>
  );
};

export default UserModal;
